import React from 'react'
import './Notfound.css';
import { useNavigate } from 'react-router-dom';
import { GoAlert } from 'react-icons/go';

export default function Notfound() {
    
    const navigate = useNavigate()

    return (
        <div>
            <h1 class="notfound"><GoAlert class="alertnotfound"/>Oups... cette page n'existe pas ! ERROR 404 NOT FOUND</h1>
            <button class="button-notfound" onClick={() => navigate("/")}>Retourner à l'accueil</button>
          
            
        </div>
    )
}
