import React, {useContext,useState, useEffect} from 'react'
import {NavLink} from 'react-router-dom'
import { ThemeContext } from '../components/Context/ThemeContext';
import './Navbar.css'
import {AiOutlineMenu} from 'react-icons/ai';
import BtnToggle from '../components/BtnToggle/BtnToggle';



export default function Navbar() {

    const {theme} = useContext(ThemeContext)
    console.log(theme);

    const [toggleMenu, setToggleMenu] = useState(false);
    const [largeur, setLargeur] = useState(window.innerWidth);

    const toggleNav = () => { setToggleMenu (!toggleMenu)}

    useEffect(() => {

        const changeWidth = () => {
            setLargeur(window.innerWidth);
        }
    
        window.addEventListener('resize', changeWidth);

        return () => {
            window.removeEventListener('resize',changeWidth);
        }
        
    }, [])

    

    return (
        <nav>
            
            {(toggleMenu || largeur > 500) && (
                
            <ul className="liste">
                <NavLink to="/react-distransics"><img class="distransicslogo" src="https://www.dis-transics.com/wp-content/uploads/2014/03/DIS-Transics_logo.png" alt="DIS-Transics"/></NavLink>
                <NavLink to="/introduction" className={({isActive}) => { return isActive ? "activeLink" : "" }}><li className="items">Introduction</li></NavLink>
                <NavLink to="/presentation" className={({isActive}) => { return isActive ? "activeLink" : "" }}><li className="items">Presentation de l'entreprise</li></NavLink>
                <NavLink to="/missions" className={({isActive}) => { return isActive ? "activeLink" : "" }}><li className="items">Missions confiées</li></NavLink>
                <BtnToggle/>

                
            </ul>
            )}
            <button onClick={toggleNav} className="btn"><AiOutlineMenu className="MenuDropdown"/></button>
        </nav>
    )
}

