import React from 'react'
import imagesDIS from '../../images/DIS.jpg';
import './Presentation.css';
import { MdHome } from 'react-icons/md';
import { GoOrganization } from 'react-icons/go';
import { BsFillFileEarmarkCodeFill, BsGraphUp } from 'react-icons/bs';
import { CgAttachment } from 'react-icons/cg';
import TxVisio from '../../images/TX-Visio.png';
import TxVisio2 from '../../images/TX-Visio2.png';
import TxVisio3 from '../../images/TX-Visio3.png';



export default function Presentation() {
    return (
        <div>
            <h1 class="presentation">Presentation de l'entreprise</h1>
            <p class="ppresentation">La société DIS est composé d'une équipe d’experts spécialisés dans le traitement des données légales des conducteurs et des véhicules dans le secteur du transport. Leur solution repose sur deux compétences majeures : la gestion des temps de services pour le secteur privé et les contrôles administratifs pour le secteur public.</p>
            <center><a><img class="imagesDIS" src={imagesDIS} ></img></a></center>
             <p class="p1presentation"><MdHome class="mdhome"/>Création de Delta Industrie Service en 1988</p>
             <p class="p2presentation"><GoOrganization class="goorganization"/>Plus de 30 employés</p>
             <p class="p3presentation"><BsFillFileEarmarkCodeFill class="editlogiciel"/>Éditeur du logiciel de contrôle sur route utilisé par les forces de l’ordre en France et dans 13 autres pays en Europe</p>
             <p class="p4presentation"><BsGraphUp class="clients"/>Plus de 4 000 clients en France et en Europe pour la collecte des données enregistrées par le chronotachygraphe et la gestion des temps de service</p>
             <p class="p5presentation"><MdHome class="mdhome"/>Siège social basé sur Saint Hilaire de Brethmas, dans le Gard</p>
             <div><p class="p6presentation"><CgAttachment class="mdhome"/>Société du groupe Transics, l’expert en Gestion de flotte du groupe ZF</p></div>

            <h2 class="gestion"> Le logiciel de gestion sociale en ligne </h2>
            <p class="p7presentation"> TX-VISIO WEB est un logiciel de gestion des temps de service en mode hébergé, 
            spécialement conçu pour un partage des données entre différents utilisateurs 
            connectés et pour une sécurité totale des données transport.</p>

            <center><a><img class="txvisio" src={TxVisio} ></img></a></center>
            <em class="txvisiotext">TX-VISIO WEB se décline en trois versions.</em>

            <h3 class="outilsdonnees"> Les outils de téléchargement des données légales du chronotachygraphe</h3>
            <p class="p8presentation"> Avec les outils de téléchargement légal de la société DIS, vous avez l’assurance d’importer correctement les fichiers c1b et v1b règlementaires.  
            Vous disposez ainsi de tous les éléments nécessaires lors d’un éventuel contrôle de police ou de gendarmerie. 
            Tous nos produits sont conçus pour vous offrir une collecte fluide des données sociales et des données légales. 
            Choisissez la technologie qui s’accorde avec vos modalités de fonctionnement !</p>
            <center><a><img class="txvisio2" src={TxVisio2} ></img></a></center>
            <center><a><img class="txvisio3" src={TxVisio3} ></img></a></center>
            

        </div>

        
    )
}
