import React from 'react'
import './Missions.css';
import logo from '../../images/logo.svg';
import { SiTerraform } from "react-icons/si";
import { FaAws , FaTruck } from "react-icons/fa";
import { RiComputerLine } from "react-icons/ri";

function App() {
    return (

        
      <div className="App">
        
        <h1 class="missions">Missions confiées</h1>
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <tr className='missions2'>
          <p class="p1missions">La mission principale est de coder un site web en React</p> 
          <p class="p2missions">avant cela nous avons pris connaissance du logiciel TX-Social et de son environnement.</p> 
          <p class="p3missions">Et ensuite nous avons pris connaissance de l'environnement AWS et Terraform.</p>
          </tr>
          <tr>
          <FaAws class="aws"/>
          <SiTerraform class="terraform"/>
          </tr>
          <tr>
          <FaTruck class="truck"/>
          <RiComputerLine class="computer"/>
          </tr>
      
        </header>
      </div>
    );
  }
  
  export default App;

